import React, {useEffect, useState} from 'react';
import axios from '../../utils/axios';
import { Link } from 'react-router-dom';

const Password = (props) => {
    // const [state, setState] = useState({
    //     margin: 0
    // });
    const [loading, setLoading] = useState(false);
    const [message, setMessage] = useState('');
    const [state, setState] = useState({
        oldPassword: "",
        password: ""
    });

    const handleSubmit = e => {
        e.preventDefault();
        setLoading(true);
        setMessage('Loading');
        axios.put('/password', state, {
            headers: {
                "X-Auth-token": localStorage.getItem('X-Auth-token')
            }
        })
        .then(response => {
            let data = response.data;
            setLoading(false);
            if(data.status == 401) {
                setMessage(data.message);
                props.history.push('/login');
            }
            if(data.status == 200) {
                setMessage('Success');
                alert('Password updated');
                props.history.push('/');
            }
            if(data.status == 400) {
                setMessage('Old password incorrect');
            }
        })
        .catch(err => {
            console.log(err)
            alert(err.message || 'something went wrong');
            setLoading(false);
            setMessage('Try again')
        });
    }

    return (
        <div id="page-wrapper" >
            <div id="page-inner">
                <div className="row text-center" style={{display: "flex", justifyContent: 'center'}}>
                    <div className="col-lg-6 col-md-8 col-sm-10 col-xs-10 text-center">
                        <h3>Change password</h3>
                        <br />
                        <div className='row'>
                            <div className="panel panel-primary">
                                <div className="panel-heading">
                                    Update your login password
                                </div>
                                <div className="panel-body">
                                    <form onSubmit={ e => handleSubmit(e) }>
                                        <div class="input-group">
                                            <span class="input-group-addon">#</span>
                                            <input type="password" class="form-control" placeholder="Old password" onChange={e => {
                                                setState(state => {
                                                    return {...state, oldPassword: e.target.value}
                                                })
                                            }} value={state.oldPassword} />
                                        </div>
                                        <br />
                                        <div class="input-group">
                                            <span class="input-group-addon">#</span>
                                            <input type="password" class="form-control" placeholder="Password" onChange={e => {
                                                setState(state => {
                                                    return {...state, password: e.target.value}
                                                })
                                            }} value={state.password} />
                                        </div>
                                        <br />
                                        <div class="input-group text-center">
                                            <button className="btn btn-success" disabled={loading}>Submit</button>
                                        </div>
                                    </form>

                                </div>
                                <div className="panel-footer">
                                    {message || 'Please do not forget your new password'}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Password;